<template>
	<view class="xh-navbar" :style="{'padding-top':deviceInfo.statusBarHeight+'px','background-color':bgColor,'background-image':bgImage}">
		<!-- 右边 -->
		<view class="xh-navbar-left">
			<slot></slot>
		</view>
		<!-- 标题文字 -->
		<text :style="{color:color}">{{title}}</text>
	</view>
</template>

<script>
	import {
		mapGetters
	} from 'vuex'
	export default {
		props: {
			title: {
				type: String,
				default: ''
			},
			bgColor: {
				type: String,
				default: ''
			},
			bgImage: {
				type: String,
				default: ''
			},
			color:{
				type: String,
				default: ''
			}
		},
		computed: {
			...mapGetters(['deviceInfo'])
		}
	}
</script>

<style lang="scss">
	.xh-navbar {
		height: 44px;
		background-color: $uni-bg-color;
		color: #FFFFFF;
		@include  flex-vh-center;
	}
</style>
